import React, { useState, useEffect } from 'react';
import {
  Box,
  Paper,
  Typography,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControlLabel,
  Switch,
  CircularProgress,
  Card,
  CardContent,
  CardActions,
  IconButton,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import { api } from '../../contexts/AuthContext';
import { Dashboard, DashboardCreate, User } from '../../types';

const DashboardManagement: React.FC = () => {
  const [dashboards, setDashboards] = useState<Dashboard[]>([]);
  const [users, setUsers] = useState<User[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [openDialog, setOpenDialog] = useState(false);
  const [newDashboard, setNewDashboard] = useState<DashboardCreate>({
    name: '',
    fields: [],
    widgets: [],
    assigned_users: [],
    is_public: false,
  });

  const fetchDashboards = async () => {
    try {
      setLoading(true);
      const response = await api.get('/dashboards/');
      setDashboards(response.data);
      setError(null);
    } catch (err) {
      setError('Failed to fetch dashboards');
      console.error('Error fetching dashboards:', err);
    } finally {
      setLoading(false);
    }
  };

  const fetchUsers = async () => {
    try {
      const response = await api.get('/users/');
      setUsers(response.data);
    } catch (err) {
      console.error('Error fetching users:', err);
    }
  };

  useEffect(() => {
    fetchDashboards();
    fetchUsers();
  }, []);

  const handleCreateDashboard = async () => {
    try {
      await api.post('/dashboards/', newDashboard);
      setOpenDialog(false);
      setNewDashboard({
        name: '',
        fields: [],
        widgets: [],
        assigned_users: [],
        is_public: false,
      });
      await fetchDashboards();
    } catch (err) {
      console.error('Error creating dashboard:', err);
    }
  };

  const handleDeleteDashboard = async (id: string) => {
    if (window.confirm('Are you sure you want to delete this dashboard?')) {
      try {
        await api.delete(`/dashboards/${id}`);
        await fetchDashboards();
      } catch (err) {
        console.error('Error deleting dashboard:', err);
      }
    }
  };

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="200px">
        <CircularProgress />
      </Box>
    );
  }

  if (error) {
    return (
      <Box p={3}>
        <Typography color="error">{error}</Typography>
      </Box>
    );
  }

  return (
    <Box p={3}>
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={3}>
        <Typography variant="h5">Dashboard Management</Typography>
        <Button variant="contained" color="primary" onClick={() => setOpenDialog(true)}>
          Create Dashboard
        </Button>
      </Box>

      <Box display="grid" gridTemplateColumns="repeat(auto-fill, minmax(300px, 1fr))" gap={3}>
        {dashboards.map((dashboard) => (
          <Card key={dashboard._id}>
            <CardContent>
              <Typography variant="h6">{dashboard.name}</Typography>
              <Typography color="textSecondary">
                {dashboard.is_public ? 'Public' : 'Private'}
              </Typography>
              <Typography variant="body2">
                {dashboard.widgets.length} widgets
              </Typography>
            </CardContent>
            <CardActions>
              <IconButton size="small" color="primary">
                <EditIcon />
              </IconButton>
              <IconButton
                size="small"
                color="error"
                onClick={() => handleDeleteDashboard(dashboard._id)}
              >
                <DeleteIcon />
              </IconButton>
            </CardActions>
          </Card>
        ))}
      </Box>

      <Dialog open={openDialog} onClose={() => setOpenDialog(false)}>
        <DialogTitle>Create New Dashboard</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label="Dashboard Name"
            fullWidth
            value={newDashboard.name}
            onChange={(e) => setNewDashboard({ ...newDashboard, name: e.target.value })}
          />
          <FormControlLabel
            control={
              <Switch
                checked={newDashboard.is_public}
                onChange={(e) => setNewDashboard({ ...newDashboard, is_public: e.target.checked })}
              />
            }
            label="Public Dashboard"
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenDialog(false)}>Cancel</Button>
          <Button onClick={handleCreateDashboard} color="primary">
            Create
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default DashboardManagement; 